.page_questions {

	.sect_nav {
		ul {

			li {

				width: auto;
				margin-bottom: 0;
			}
		}
	}

	.sect_nav.scroll-active {
		position: fixed;
		// top: rem(76);
		left: 0;
		width: 100%;
		background-color: white;
		z-index: 10;
	}

	&__sect {
		padding: 0 rem(200) rem(72) rem(200);

		&:first-child {
			padding: rem(72) rem(200);
		}
	}
}