.slider_article {
	>.container {
		width: 1220px;
	}
	h2 {
		// text-align: center;
		margin-bottom: rem(20);
	}

	p {
		font-size: rem(18);
		max-width: rem(550);

		margin-left: auto;
		margin-right: auto;
	}

	.swiper-nav {
		display: none;
	}

	.swiper-wrapper {
		flex-wrap: wrap;
		justify-content: center;

		.swiper-slide {
			width: calc((100% - 4rem) / 3);
			margin-right: rem(32);

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&__item {
		img {
			height: rem(190);
		}
	}
}