.page_research_article {
    .container {
        width: 1220px;
        display: flex;
        flex-wrap: wrap;
        margin-top: rem(56);
    }
    .w-8\/12 {
        flex: 0 0 66.666667%;
        max-width: 66.666667%
    }
    .w-4\/12 {
        flex: 0 0 33.333333%;
        max-width: 33.333333%
    }
    .button_active_aside {
        display: none;
    }
    .article {
        margin-left: 0;
        margin-right: rem(36);
        h1 {
            display: none;
        }
        &__foot {
            margin-bottom: rem(88);
        }
    }
    .aside {
        background-color: var(--c-light-blue);
        padding: rem(24) rem(14) rem(24) rem(24);
    }
    .catalog__left {
        height: 100vh;
        flex: 0 0 100%;
        max-width: 100%;
        background-color: transparent;
        &-nav {
            ul {
                flex: 0 0 100%;
                max-width: 100%;
                margin-right: 0;
                li {
                    a {
                        color: var(--c-dark);
                        display: flex;
                        justify-content: space-between;
                        border: 0;
                        margin-bottom: 0;
                        line-height: 152%;
                        &:hover {
                            color: var(--c-dark);
                            border-bottom: 0;
                        }
                    }
                }
            }
        }
    }
}