.header {
    height: rem(940);
    background: url(../img/header_bg_x3_.jpg) no-repeat top center;
    background-size: cover;
    >.container {
        // max-width: 1400px;
        width: calc(100% - 40px);
    }
    &__top {
        .header_bay,
        .header_logo {
            flex-basis: 33%;
        }
        .header_logo {
            display: flex;
            justify-content: center;
        }
        .header_bay {
            display: flex;
            justify-content: flex-end;
            .button_primary {
                display: block;
            }
            .icon {
                display: none;
            }
        }
    }
    &__bottom {
        position: relative;
        margin-top: rem(100);
        h1,
        p,
        a {
            position: relative;
            z-index: 1;
        }
        h1 {
            margin-bottom: rem(28);
            max-width: rem(1000);
        }
        p {
            margin-bottom: rem(48);
            max-width: rem(540);
        }
        .header__picture {
            position: absolute;
            top: rem(168);
            order: 999;
            width: 1200px;
            // z-index: 0;
            img {
                width: 100%;
            }
        }
    }
    .logo {
        display: block;
        img {
            width: 100%;
            height: auto;
            object-fit: none;
        }
    }
    &_menu {
        flex-basis: 33%;
        // width: 35%;
        &__list {
            position: relative;
            top: auto;
            left: auto;
            background-color: transparent;
            display: flex;
            padding: 0;
            >ul {
                display: flex;
                flex-wrap: wrap;
                >li {
                    padding: 0;
                    .header_menu__child {
                        position: absolute;
                        top: rem(56);
                        left: 0;
                        // display: flex;
                        // flex-wrap: wrap;
                        justify-content: space-between;
                        background-color: transparent;
                        padding-top: 0;
                        height: auto;
                        width: rem(856);
                        background-color: white;
                        padding: 0;
                        border-radius: rem(4);
                        padding: 0;
                        display: none;
                        // overflow: hidden;
                        &::before {
                            content: "";
                            position: absolute;
                            top: rem(-8);
                            left: rem(42);
                            width: 0;
                            height: 0;
                            border: rem(8) solid transparent;
                            border-bottom-color: white;
                            border-top: 0;
                        }
                        &-left {
                            display: flex;
                            // flex-wrap: wrap;
                            padding: rem(29) 0 rem(29) rem(32);
                            ul {
                                // display: flex;
                                // flex-direction: column;
                                // flex-wrap: wrap;
                                // column-count: 3;
                                // 
                                flex-grow: 1;
                                flex-shrink: 0;
                                margin-right: rem(50);
                                &:first-child {
                                    flex: 0 0 rem(125);
                                }
                                li {
                                    padding: rem(8) 0;
                                    &:first-child {
                                        padding-top: 0;
                                    }
                                    &:last-child {
                                        padding-bottom: 0;
                                    }
                                    &:nth-child(4) {
                                        break-after: always;
                                    }
                                    a {
                                        text-transform: none;
                                        // font-size: rem(14);
                                    }
                                }
                                .desktop {
                                    display: block;
                                }
                            }
                        }
                        &-right {
                            flex-grow: 1;
                            display: flex;
                            flex-direction: column;
                            flex: 0 0 rem(285);
                            a {
                                &:first-child {
                                    margin-bottom: rem(1);
                                }
                            }
                        }
                    }
                    .header_menu__child.active_desktop {
                        display: flex;
                    }
                    margin-right: rem(32);
                    a {
                        font-weight: 700;
                        text-transform: uppercase;
                        i {
                            display: none;
                        }
                    }
                }
                >li:nth-child(4),
                >li:nth-child(5),
                >li:nth-child(6) {
                    display: none;
                }
            }
        }
    }
    &_burger {
        display: none;
    }
}

.header_fixed {
    &::before {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: rem(76);
        background-color: white;
        z-index: 10;
    }
    .header__top {
        // max-width: 1400px;
        width: calc(100% - 40px);
        position: fixed;
        top: 0;
        left: auto;
        padding: rem(10) 0;
        z-index: 12;
        // background-color: #D5E4F3;
    }
}

.header_menu_active_child {
    .header_menu__child {
        overflow: unset
    }
}