.you_know {
    background-color: transparent;
    height: auto;
    margin-top: rem(100);
    padding: rem(84) 0;
    &__wrap {
        position: relative;
        background-color: var(--c-dirty-blue);
        width: rem(900);
        height: rem(492);
        .image {
            top: rem(-100);
            left: 0;
            width: rem(421);
            height: rem(514);
        }
        .text {
            position: absolute;
            top: rem(60);
            right: rem(-215);
            padding-top: 0;
            h3 {
                max-width: rem(585);
            }
        }
    }
}