#header_goods_about {
    // height: 100%;
    >.container {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .header__top {
        position: relative;
        z-index: 2;
        width: 100%;
        max-width: 1400px;
        // position: absolute;
        // top: 0;
        // left: 0;
        // width: 100%;
        // z-index: 10;
        // margin-left: 20px;
        // margin-right: 20px;
        background-color: rgba(231, 238, 248, .85);
        padding: rem(12) rem(20);
        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(231, 238, 248, .85);
        }
        &::before {
            left: -100%;
        }
        &::after {
            right: -100%;
        }
    }
    .header__bottom {
        // width: 100%;
        margin-top: 0;
        flex-direction: row;
        align-items: flex-start;
        // justify-content: center;
        display: grid;
        width: 100%;
        grid-template-columns: calc((100% - 1220px)/2) 6fr 4fr calc((100% - 1220px)/2);
        margin-bottom: rem(-110);
        &-right {
            // flex: 0 0 rem(486);
            // max-width: rem(486);
            grid-column-end: span 2;
        }
        &-left {
            grid-column: 1 / span 2;
            position: relative;
            top: rem(-110);
            // z-index: -1
            // flex: 0 0 58;
            // max-width: 58%;
            height: rem(940);
            background: url(../img/page_goods_about/bg_gooda.jpg) no-repeat top right;
            background-size: cover;
            padding-bottom: 0;
            padding-top: rem(80);
            justify-content: flex-end;
            .swiper-overlay {
                width: rem(591);
                height: rem(591);
                margin-left: rem(30);
                margin-right: rem(100);
                .swiper-container {
                    padding-bottom: rem(70);
                    margin-top: rem(125);
                    // height: rem(250);

					.swiper-wrapper {
						width: rem(500);
					}

					.swiper-slide {
						width: rem(500);
						// display: flex;
						// justify-content: center;

						// img {
							// width: 100%;
						// }
					}
                }
            }
            .swiper-nav {
                bottom: 0;
                // margin-left: rem(-42);
            }
			
        }
        &-right {
            margin: rem(60) 0 0 0;
            width: rem(486);
            padding-left: rem(32);
            >span {
                font-size: rem(13);
            }
            h3 {
                font-size: rem(44);
            }
            p {
                margin-bottom: rem(16);
            }
            .icons {
                margin-right: rem(20);
                i {
                    font-size: rem(25);
                }
            }
            .count {
                font-size: rem(14);
            }
            a.button_primary {
                order: 7;
                margin-top: 0;
            }
            ul {
                order: 5;
                margin-top: rem(48);
                margin-bottom: rem(56);
            }
            .all {
                order: 6;
                margin-bottom: rem(46);
            }
        }
    }
}

#header_goods_about.header_fixed {
    >.container {
        max-width: 1400px;
        width: calc(100% - 40px);
    }
    .header__top {
        max-width: 1400px;
        width: calc(100% - 40px);
        position: fixed;
        top: 0;
        left: auto;
        padding: rem(10) 0;
        z-index: 10;
        background-color: #D5E4F3;
        margin-left: auto;
        margin-right: auto;
        &::before,
        &::after {
            display: none;
        }
    }
}