#header_research_open {
	height: rem(260);
	background: url(../img/page_research_open/header_bg_x3.jpg) no-repeat top center;
	background-size: cover;

	.header__top {
		padding-top: rem(10);
	}

	.header__bottom {
		display: flex;
		margin-top: rem(50);
		padding-bottom: 0;

		h1 {
			font-weight: normal;
			font-size: rem(44);
		}
	}
}

#header_research_open.header_fixed {
	.header__top {
		padding-bottom: rem(10);
	}
}