.page_article {

	>.container {
		display: flex;
		flex-wrap: wrap;
	}

	.w-8\/12 {
		flex: 0 0 66.666667%;
		max-width: 66.666667%
	}

	.w-4\/12 {
		flex: 0 0 33.333333%;
		max-width: 33.333333%

	}

	.article {
		
	}

	.aside {
		margin-left: rem(32);
		margin-right: 0;
		margin-top: 0;
		// padding-left: 0;
		// padding-right: 0;

		padding: rem(24) rem(22);

		h3 {
			margin-top: rem(12);
			margin-bottom: rem(34);

			align-self: flex-start;
		}

		.swiper-nav {
			display: none;
		}

		.swiper-wrapper {
			display: flex;
			flex-direction: column;

			.swiper-slide {
				margin-bottom: rem(24);
				height: auto;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.articles__items {
			margin-bottom: 0;
			.item {
				flex: 0 0 100%;
				max-width: 100%;

				.inside_title {
					font-size: rem(18);
				}
			}
		}
	}

}