.page_blog {
    .sect_nav {
        padding: rem(24) 0 rem(8) 0;
        ul {
            li {
                width: auto;
            }
        }
    }
    .w-full {}
    .articles {
        padding: rem(72) 0;
        .item {
            flex: 0 0 calc((100% - 2rem) /2);
            max-width: calc((100% - 2rem) /2);
            margin-right: rem(32);
            margin-bottom: rem(32);
            border-bottom: rem(1) solid #E5E8EC;
            &:nth-child(2n) {
                margin-right: 0;
            }
            &:last-child {
                margin-bottom: rem(32);
            }
            &__top {
                height: rem(300);
            }
        }
    }
}

.page_blog.position_scroll {
    &::before {
        height: rem(74);
    }
}

.default_subscribe {
    .subscribe__form {
        padding: 0;
        max-width: rem(600);
        margin-left: auto;
        margin-right: auto;
        .field {
            width: 80%;
        }
    }
}