.numbers {
    &__top {
        height: auto;
        display: flex;
        justify-content: center;
        margin-bottom: rem(84);
        video {
            width: rem(800);
            height: rem(450);
            clip-path: none;
        }
    }
    &__bottom {
        top: auto;
        z-index: 1;
        background: url(../img/section_numbers_bg_x3.jpg) no-repeat top center;
        background-size: cover;
        &::before {
            display: none;
        }
        h2,
        h3 {
            max-width: 100%;
        }
        p.text {
            margin-bottom: rem(72);
        }
        &-items {
            padding-left: 0;
            padding-right: 0;
            justify-content: center;
            .item {
                flex: 0 0 25%;
                margin-bottom: 0;
            }
        }
    }
}