.popup_modify {
	height: 100%;

	padding: rem(60) 0;

	.popup_overlay__wrap {
		height: auto;

		width: rem(620);

		padding: rem(80) rem(68);

		h3 {
			font-size: rem(28);
			margin-bottom: rem(56);
		}

		.items {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			.item {
				display: flex;
				align-items: center;
				width: calc((100% - 10.7rem) / 2);
				height: rem(68);
				margin-right: rem(85);
				margin-bottom: rem(40);

				&:nth-child(2), &:nth-child(4), &:last-child {
					margin-right: 0;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}

